import Vue from 'vue'
import VRouter from 'vue-router'
import VResource from 'vue-resource'

import App from './App'
import Loading from './components/loading'
import MDetail from './pages/MDetail'
import Actor from './pages/Actor'
import MList from './pages/MList'
import MNav from './pages/MNav'
import Tooltip from './components/Tooltip'
import Bus from './bus.js'

Vue.use(VRouter);
Vue.use(VResource);
Vue.use(Loading);

const routes = [
	{path:'/',redirect:'/mnav'},
	{path:'/detail/:id',component:MDetail},
	{path:'/actor/:id',component:Actor},
	{path:'/tooltip',component:Tooltip},
	{path:'/mlist',component:MList,name:'mlist'},
	{path:'/mnav',component:MNav},
]
const router = new VRouter({
	mode:'history',
	routes
});

var vm = new Vue({
	el:'#app',
	router,
	template:'<App/>',
	components:{ App },
	// render:(cb) => cb(App)
	// render:function(cb){
	// 	return cb(App2);
	// }
	data:{
		showLoaidng:1111
	}
});

// 拦截器
Vue.http.interceptors.push((request,next) => {
	Bus.$emit('toggleLoading',true);
	next((response) => {
		if(response.status == 200){
		}else if(response.status == 500){

			vm.$router.push({path:'/Tooltip'});
		}
		Bus.$emit('toggleLoading',false);
		return response;
	})
});


