<template>
	<div class="main-box" ref='mainBox'>
		<MyHeader></MyHeader>
		<div class="main">
			<transition name="fade">
				<router-view v-cloak></router-view>
			</transition>
		</div>
		<Bg :bgurl="bgurl"></Bg>
		<MyFooter></MyFooter>
		<loading v-show="isShowLoading"></loading>
	</div>
</template>

<script>
	import MyHeader from './components/MyHeader'
	import MyFooter from './components/MyFooter'
	import Bg from './components/Bg'
	import MDetail from './pages/MDetail'
	import Bus from './bus'
	export default {
		components:{
			MyHeader,
			MyFooter,
			MDetail,
			Bg
		},
		created(){
			Bus.$on('toggleLoading',(show) => {
				this.isShowLoading = show;
			});
			Bus.$on('getBgUrl',(_bgurl) => {
				this.bgurl = _bgurl
			})
		},
		data(){
			return {
				isShowLoading:false,
				bgurl:''
			}
		},
		mounted(){
			var el = this.$refs.mainBox;
			el.style.width = 10 + 'rem';
			el.style.height = window.innerHeight + 'px';
			window.addEventListener('resize', function () {
		    	setTimeout(() => {el.style.height = window.innerHeight + 'px';}, 300);
			}, false);
		}
	}
</script>

<style lang=scss>
@import './assets/styles/_var';
@import './assets/styles/_mixin';

@include reset; /*重置样式*/

img{
	display: block;
	width: 100%;
}
h3{
	padding-left: rem(10px);
	margin: rem(18px) 0;
	color:#fff;
}
[v-cloak] { 
	display: none 
}
.main-box{
	position: relative;
	margin: 0 auto;
	background-color: #000;
	overflow: hidden;
}
.main{
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	padding: rem(55px) 0 rem(75px);
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
	z-index: 1
}
.m-list-p{
	overflow: hidden;
	li{
		float: left;
		padding: rem(10px);
		width: 33.33%;
		text-align: center;
		box-sizing: border-box;
		.img-box{
			height: rem(270px);
			overflow: hidden;
		}
		p{
			margin: rem(10px) 0;
			@include word-nowap;
			color: #fff;
		}
	}
}
.fade-enter-active,.fade-leave-active{
	transition: opacity .3s;
}
.fade-enter,.fade-leave-to{
	opacity: 0;
}
</style>


