<template>
	<div>
		<div class="actor-box" v-if="actorData">
			<h3>演员信息：</h3>
			<section>
				<div class="cover-box"><img :src="actorData.avatars.large"></div>
				<ul class="text-info">
					<li><strong>中文姓名：</strong><span>{{actorData.name}}</span></li>
					<li><strong>其他称呼：</strong><span v-for="item in actorData.aka_en">{{item}}</span></li>
					<li><strong>出生地：</strong><span>{{actorData.born_place}}</span></li>
					<li><strong>简介：</strong><span>...</span></li>
				</ul>
			</section>
			<h3>其他作品：</h3>
			<section>
				<ul class="m-list-p">
					<li v-for="item in actorData.works">
						<router-link :to="'/detail/' + item.subject.id">
							<div class="img-box"><img :src="item.subject.images.large"></div>
							<p>{{item.subject.title}}</p>
						</router-link>
					</li>
				</ul>
			</section>
		</div>
	</div>
</template>

<script>
	export default{
		created(){
			this.$http.jsonp('http://api.douban.com/v2/movie/celebrity/' + this.$route.params.id)
			.then((response) => {
				this.actorData = response.data;
			})
			.catch((response) => {
				
			})
		},
		data(){
			return {
				actorData:null
			}
		}
	}
</script>

<style lang=scss>
@import './../assets/styles/_mixin';
	.actor-box{
		color: #fff;
		section{
			padding: 0 rem(12px);
			margin-bottom: rem(50px);
			overflow: hidden;
		}
		.cover-box{
			float: left;
			width: rem(150px);
			height: rem(200px);
			overflow: hidden;
			margin: 0  rem(14px) rem(5px) 0;
		}
		.text-info{
			line-height: rem(40px);	
			li{
				@include word-break;
			}
		}
	}
</style>


