<template>
	<div class="m-nav">
		<div ref="smile">
			<p>
				<!-- 如果路由跳转属性写path，下级路由会获取不到params参数，所以这里用name指定路由 -->
				<router-link :to="{name:'mlist',params:{portName:'inTheaters'}}">正在热映</router-link>
				<router-link :to="{name:'mlist',params:{portName:'top250'}}">全球排名</router-link>
			</p>
			<p>
				<router-link :to="{name:'mlist',params:{portName:'usBox'}}">北美票房</router-link>
				<router-link :to="{name:'mlist',params:{portName:'comingSoon'}}">即将上映</router-link>
			</p>
			<div class="mouth"></div>
			<div class="disc">
				<div class="disc-img"><img src="./../assets/imgs/disc.png"></div>
				<div class="disc-line"></div>
			</div>
		</div>
	</div>
</template>

<script>
	import Bus from './../bus' 
	export default {
		created(){
			/*let itemList=['inTheaters','comingSoon','top250','usBox'];
			let params = {start:0,count:1};
			for(let item in this.mNavDatas){
				if(item == 'comingSoon'){
					params = {start:0,count:20};
				}
				this.getData(this.mNavDatas[item]._url,params,item);
			}*/
		},
		mounted(){
			/*getData(_url,params={},dataObj){
				this.$http.jsonp(_url,params)
				.then((response) => {
					this.mNavDatas[dataObj]._data = response.data;
				})
				.catch((response) => {
					console.log(response);
				});
			},*/
			setTimeout(() => {
				this.$refs.smile.setAttribute('class','smile');
			});
			Bus.$emit('getBgUrl','http://img3.doubanio.com/img/celebrity/medium/56339.jpg');
		},
		data(){
			return {}
			/*return {
				mNavDatas:{
					inTheaters:{
						_url:'http://api.douban.com/v2/movie/in_theaters',	//正在热映
						// _url:'./../../static/dd.json',	//正在热映
						_data:null
					},
					comingSoon:{
						_url:'http://api.douban.com/v2/movie/coming_soon',	//即将上映
						_data:null
					},
					top250:{
						_url:'http://api.douban.com/v2/movie/top250',	//top250
						_data:null
					},
					usBox:{
						_url:'http://api.douban.com/v2/movie/us_box',	//北美票房榜
						_data:null
					}
				}
			}*/
		}
	}
</script>
<style lang=scss>
@import './../assets/styles/_mixin';
.m-nav{
	position: absolute;
	left: 0;
	top: 30%;
	width: 100%;
	p{
		@include word-nowap;
		position: absolute;
		left:0;
		top:0;
		width: 100%;
		text-align: center;
		transition: all linear 500ms;
		a{
			color:#fff;
			margin: 0 rem(50px);
			font-size: rem(42px);
		}
	}
	.smile{
		font-size: 12px;
		p{
			&:nth-child(1){
			transform: rotateZ(25deg);
			};
			&:nth-child(2){
				transform: rotateZ(-25deg);
			};
		}
	}
	.mouth{
		width: rem(50px);
		height: rem(30px);
		border-radius: rem(30px) rem(5px) rem(30px) rem(30px);
		border:2px solid #fff;
		margin: rem(190px) auto 0;
		text-align: center;
		color:#fff;
	}
	.disc{
		margin-top: rem(80px);
		border-bottom: 2px solid #383933;
		.disc-img{
			width: rem(120px);
			border:2px solid #383933;
			border-radius:rem(120px);
			transform-origin: center center;
			animation:discAniRotate 5000ms linear infinite;
			@at-root(without:all){
				@keyframes discAniRotate{
					0%{
						transform: translateX(-100%) rotate(0deg);
					}
					100%{
						transform: translateX(10rem)  rotate(700deg);
					}
				}
			}
		}
	}
}
</style>


