<template>
	<div class="tooltip-box">
		<div>由于第三方数据接口请求次数限制，页面啥也没有啦！一起等等吧！！！</div>
	</div>
</template>
<script>
	export default {
		created(){
			this.$root.showLoading = false;
		}
	}
</script>
<style>
	.tooltip-box{
		position: fixed;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background-color: rgba(0,0,0,.5);
	}
	.tooltip-box div{
		position: absolute;
		top: 43%;
		width: 100%;
		color: #fff;
		text-align: center;
		font-size: .55rem;
		padding: 0 12px;
	}
</style>


