<template>
	<div class="m-slide-list">
		<div  class="no-data" v-if="!isShowSlider" :data-box="dataList[0]">没有数据哦！</div>
		<ul v-else>
			<template  v-for="item in dataList">
				<router-link v-if="item.images || item.avatars" :to=" routerLocation + item.id" tag="li">
					<div class="img-box">
						<img :src="item.images ? item.images.medium : item.avatars.medium  | formatSwop">
					</div>
					<p>{{item.title || item.name}}</p>
				</router-link>
			</template>
		</ul>
	</div>
</template>

<script>
	export default {
		props:['dataList','routerLocation'],
		data(){
			return{
				isShowSlider:false,
				tooptip:'没有数据哦！'
			}
		},
		created(){
			this.showList();
		},
		beforeUpdate(){
			console.log(123);
			this.showList();
		},
		methods:{
			showList(){
				let couter = 0;
				console.log(this.dataList);
				for(var i in this.dataList){
					if(this.dataList[i].images || this.dataList[i].avatars){	//如果热映有图片或者主演数据有图片就渲染列表，否则不渲染列表输出提示
						this.isShowSlider = true;
						return;
					}
				}
			}
		},
		filters:{
			formatSwop(str){
				if(str.indexOf('.webp') >= 0){
					return str.replace('.webp','.jpg')
				}
				return str;
			}
		},
	}
</script>

<style lang=scss>
@import './../assets/styles/_var';
@import './../assets/styles/_mixin';
	.m-slide-list{
		ul{
			overflow-x: auto;
			overflow-y: hidden;
			height: rem(270px);
			white-space: nowrap;
			-webkit-overflow-scrolling: touch;
			font-size: 0;
		}
		li{
			display: inline-block;
			width: rem(150px);
			margin: 0 rem(6px);
			padding: 3px;
			&.nodata{
				text-align: center;
				font-size: rem(22px);
				padding:rem(20px) 0;
			}
			&.router-link-exact-active{
				padding:0;
				border:3px solid #fff;
			};
			.img-box{
				height: rem(200px);
				overflow: hidden;
			}
			p{
			    font-size: rem(22px);
			    text-align: center;
			    margin: rem(10px) 0;
			    @include word-nowap;
			}
		}
		.no-data{
			text-align: center;
			padding: rem(40px) 0;
		}
	}
</style>


