<template>
	<div class="m-list-box" v-if="dataBox">
		<h3>{{dataBox.title}}</h3>
		<ul class="m-list-p">
			<li v-for="item in dataBox.subjects" v-if="!item.subject">
				<router-link :to="'/detail/' + item.id">
					<div class="img-box">
						<img :src="item.images.medium">
					</div>
					<p>{{item.title}}</p>
				</router-link>
			<li v-else="item.subject">
				<router-link :to="'/detail/' + item.subject.id">
					<div class="img-box">
						<img :src="item.subject.images.medium">
					</div>
					<p>{{item.subject.title}}</p>
				</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
	export default {
		beforeRouteEnter(to,from,next){
			next((vm) => {
				vm.diffPortName(to.params);
			});
		},
		methods:{
			diffPortName(toParams){
				switch(toParams.portName){
					case 'inTheaters':
						this.getData('http://api.douban.com/v2/movie/in_theaters');
						break;
					case 'comingSoon':
						this.getData('http://api.douban.com/v2/movie/coming_soon');
						break;
					case 'top250':
						this.getData('http://api.douban.com/v2/movie/top250');
						break;
					case 'usBox':
						this.getData('http://api.douban.com/v2/movie/us_box');
						break;
					default:
						this.getData('http://api.douban.com/v2/movie/in_theaters');
						console.log('HOTHOTHOT');
				}
			},
			getData(_url,params={}){
				this.$http.jsonp(_url,params)
				.then((response) => {
					this.dataBox = response.data;
					console.log(response.data);
				})
				.catch((response) => {

				})
			}
		},
		data(){
			return {
				dataBox:null
				/*mNavDatas:{
					inTheaters:{
						_url:'http://api.douban.com/v2/movie/in_theaters',	//正在热映
						// _url:'./../../static/dd.json',	//正在热映
						_data:null
					},
					comingSoon:{
						_url:'http://api.douban.com/v2/movie/coming_soon',	//即将上映
						_data:null
					},
					top250:{
						_url:'http://api.douban.com/v2/movie/top250',	//top250
						_data:null
					},
					usBox:{
						_url:'http://api.douban.com/v2/movie/us_box',	//北美票房榜
						_data:null
					}
				}*/
			}
		}
	}
</script>


