<template>
<div class="detail-box" v-if="detailData">
	<section class="hot-m-list">
		<h3>正在热映</h3>
		<MSlideList v-if="hotList" :dataList="hotList.subjects" :routerLocation="'/detail/'"></MSlideList>
	</section>
	<section class="hot-m-intro">
		<h3>影片详情：</h3>
		<div class="m-cover-text white-bg-box">
			<div class="cover-img-box"><img :src="detailData.images.large"></div>
			<div class="cover-text">
				<p><strong>片名：</strong><span>{{detailData.title}}</span></p>
				<p><strong>地区：</strong><span v-for="item in detailData.countries">{{item}}、</span></p>
				<p><strong>影片类型：</strong><span v-for="item in detailData.genres">{{item}}、</span></p>
				<p><strong>上映年份：</strong><span>{{detailData.year}}</span></p>
				<p><strong>故事背景：</strong></p>
				<p class="text-info">
					{{detailData.summary ? detailData.summary : '暂时没有简介哦！'}}
				</p>
			</div>
		</div>
		<div class="m-actors white-bg-box">
			<h4>主演：</h4>
			<MSlideList :dataList="detailData.casts" :routerLocation="'/actor/'"></MSlideList>
		</div>
	</section>
</div>
</template>

<script>
	import MSlideList from './../components/MSlideList'
	import Bus from './../bus'
	export default{
		components:{
			MSlideList
		},
		data(){
			return {
				hotList:null,		//热映列表数据
				detailData:null,		//影片详情数据
				// mHotPort:'http://api.douban.com/v2/movie/in_theaters',
				mHotPort:'./../../static/dd.json',
				mDetailPort:'http://api.douban.com/v2/movie/subject/'
			}
		},
		beforeRouteEnter(to,from,next){
			next((vm) => {
				vm.getData(vm.mHotPort,vm,'hotList',{start:0,count:10},'get');   //调用热映接口

				let _url = vm.mDetailPort + to.params.id;
				vm.getData(_url,vm,'detailData');	//调用影片详情接口
			})
			
		},
		beforeRouteUpdate(to,from,next){
			let _url = this.mDetailPort + to.params.id;
			this.getData(_url,this,'detailData');
			next();
		},
		methods:{
			getData(_url,_this,databox,_params={},request='jsonp'){	
			//公用获取调用接口函数(接口地址，上下文对象，数据接收属性，参数，请求方式(默认jsonp))
				_this.$http[request](_url,{
					params:_params
				})
				.then((response) => {
					_this[databox] = response.data;
					let avatarsObj = response.data;
					if(avatarsObj.images){	//把影片详情的封面作为主背景
						Bus.$emit('getBgUrl',avatarsObj.images.large);
					}
				})
				.catch((response) => {
					
				});
			}
		},
		mounted(){

		}
	}
</script>

<style lang=scss>
@import './../assets/styles/_mixin';
	.detail-box{
		overflow: hidden;
		background-size: 100% 100%;
		background-repeat: no-repeat;
		background-attachment:fixed
		section{
			margin-bottom: rem(34px);
		}
	}
	.hot-m-list{
		color: #fff;
	}
	.hot-m-intro{
		color:#233023;
	}
	.m-cover-text{
		overflow: hidden;
		.cover-img-box{
			float: left;
			width: rem(150px);
			height: rem(200px);
			overflow: hidden;
			margin: 0  rem(14px) rem(5px) 0;
		}
		.cover-text{
			p{
				@include word-break;
				margin-bottom: rem(5px);
				&.text-info{
					text-indent: 2em;
					line-height: rem(32px)
				}
			}
		}
	}
	.white-bg-box{
		padding: rem(12px);
		border-radius:8px;
		background-color: #fff;
		width: 94%;
		margin: 0 auto rem(24px);
		h4{
			margin: 0 0  rem(5px);
			font-size: rem(22px)
		}
	}
</style>


